import React, { useEffect } from "react";
import SectionTitle from "../../components/site/SectionTitle";
import { Col, Container, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { fetchTrainers } from "../../services/trainers.service";
import FullScreenLoading from "../../components/shared/Loading/FullScreenLoading";
import { API_LINK_STORAGE } from "../../services/ApiService";

const Trainers = () => {
  const dispatch = useDispatch();

  const { trainers, trainersLoading, trainersError } = useSelector(
    (state) => state.trainers
  );

  useEffect(() => {
    dispatch(fetchTrainers());
  }, [dispatch]);

  return (
    <FullScreenLoading loading={trainersLoading} error={trainersError}>
      <section id="trainersSection">
        <Container>
          <SectionTitle title="المدربين" />
          <Row>
            {trainers.map((item, idx) => {
              return (
                <Col key={idx} lg={4} md={6} sm={12}>
                  <div class="card">
                    <div class="card-content">
                      <div class="card-img">
                        <img src={API_LINK_STORAGE + item.img_path} />
                      </div>
                      <div class="trainer-name">
                        <h2>{item.ar_name}</h2>
                      </div>
                      <div class="trainer-job">
                        <p>{item.description}</p>
                      </div>
                      <div class="trainer-mail"></div>
                      <div className="social-accounts">
                        {item.facebook_link ? (
                          <a href={item.facebook_link}>
                            <i className="fa-brands fa-facebook-f"></i>
                          </a>
                        ) : (
                          ""
                        )}
                        {item.whatsapp_link ? (
                          <a href={item.whatsapp_link}>
                            <i className="fa-brands fa-whatsapp"></i>
                          </a>
                        ) : (
                          ""
                        )}
                        {item.telegram_link ? (
                          <a href={item.telegram_link}>
                            <i className="fa-brands fa-telegram"></i>
                          </a>
                        ) : (
                          ""
                        )}
                        {item.linkedin_link ? (
                          <a href={item.linkedin_link}>
                            <i className="fa-brands fa-linkedin"></i>
                          </a>
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  </div>
                </Col>
              );
            })}
          </Row>
        </Container>
      </section>
    </FullScreenLoading>
  );
};

export default Trainers;
